<template>
    <div class="userEdit">
        <Header title="账号管理" />
        <div class="input-item">
            <van-field v-model="nickName" label="昵称" placeholder="请输入昵称" />
            <van-field v-model="desc" label="个性签名" placeholder="请输入个性签名" />
            <van-field v-model="password" label="密码" placeholder="请输入密码" />
        </div>
        <van-button round block type="primary" @click="save" class="save-btn">
            完成
        </van-button>
        <van-button round block type="primary" @click="logout" class="save-btn">
            退出
        </van-button>
    </div>
</template>
<script >
import Header from "@/components/Header.vue"
import { reactive ,toRefs} from "vue"
import {useRouter} from "vue-router"
import { showToast } from 'vant';
export default {
    components: {
        Header
    },
    setup(){
        let router=useRouter()
        let data=reactive({
            nickName:"",
            desc:"干饭人",
            password:""
        })
        let logout=()=>{
            localStorage.removeItem("isLogin")
            showToast("退出成功")
            router.push("/home")
        }

        let save=()=>{
            if(data.nickName && data.password){
                let userInfo = JSON.parse(localStorage.userInfo)
                const newUserInfo={
                    "用户名":data.nickName || userInfo["用户名"],
                    "密码":data.password || userInfo["密码"]
                }
                localStorage.setItem("userInfo",JSON.stringify(newUserInfo))
                showToast("保存成功")
                logout()
            }else{
                showToast("请输入要修改的信息")
            }
        }

        return{
            ...toRefs(data),
            logout,
            save
        }
    }
}
</script>
<style scoped lang="less">
.save-btn{
    width: 80%;
    margin: 20px auto;
}

</style>